    display: flex;
    justify-content: space-between;
    align-items: center;
  }


  header button {
    background-color: var(--bckgrnd-clr);
  }

  .switch-theme {
    display: flex;
    align-items: center;
    border-radius: 10px;
    font-weight: bold;
    border: 1px solid var(--txt-clr);
  }


  .switch-theme svg {
    width: 2rem;
