  }


  .due-date {
    color: var(--date);
    font-size: 1.1rem;
    font-weight: bold;
  }

  .right svg {
    width: 2rem;
    display: inline-block;
    vertical-align: middle;
    margin-right: 0.4rem;
    fill: var(--txt-clr);
  }

  .right p {
    text-align: left;
  }


  textarea {
    resize: none;
  }

  .add-task:hover {
    background-color: var(--add-task);
  }

  .todo input {
    margin-right: 1rem;
    scale: 1.5;
  }

  .detail {
    font-size: 1.2rem;
    padding: 0 2.5rem;
  }


  .task-priority {
    font-weight: bold;
  }

  .checkbox input {
    margin-top: 1rem;
  }


  footer {
    text-align: center;
    background-color: var(--nav-color);
    padding: 0.3rem;
    font-weight: bold;
  }

  .low {
    background-color: var(--low);
  }

  .normal {
    background-color: var(--normal);
  }

  .high {
    background-color: var(--high);
  }

  .selected {
    background-color: var(--highlight-clr) !important;
    color: var(--svg-hover);
    fill: var(--svg-hover) !important;
  }

  .edited {
    border : 5px solid red;
  }

  .completed-task {
    background-color: var(--complete);
  }

  .important-task {
    fill: yellow;
    scale: 1.5;
  }


  input[type="radio"]{
    scale: 1.6;
    margin-left: 4px;
  }

  input[type="date"]::-webkit-calendar-picker-indicator {
      filter: invert(17%) sepia(86%) saturate(7492%) hue-rotate(0deg) brightness(94%) contrast(116%);
  }

  .add-task-ui label {
    display: inline-block;
    font-weight: bold;
    min-width: 4rem;
  }

  .text {
    width: min(25rem, 45rem);
  }

  input[type="checkbox"]{
    appearance: none;
    background-color: white;
    width: 1rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    position: relatve;
    border: 1px solid grey;
  }


  input[type="checkbox"]::before {
    display: block;
    content: "";
    font-size: 1.2rem;
    color: green;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    visibility: hidden;
    position: relative;
    bottom: 40%;
    right: 5%;
  }

  input[type="checkbox"]:checked::before {
    visibility: visible;
