eo, canvas, svg {\n    display: block;\n    max-width: 100%;\n  }\n  \n  input, button, textarea, select {\n    font: inherit;\n    color: inherit;\n  }\n  \n  p, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n  }\n  \n  #root, #__next {\n    isolation: isolate;\n  }\n\n  :root.light {\n    --bckgrnd-clr: white;\n    --txt-clr: black;\n    --highlight-clr: rgba(255, 166, 0, 1);\n    --nav-color: beige;\n    --svg-hover: white;\n    --input-txt: black;\n    --input-clr: white;\n    --focus-clr: black;\n    --dynamic-btn-clr: rgba(128, 128, 128, 0.7);\n    --high: rgba(238, 10, 10, 0.4);\n    --normal: rgba(0, 0, 255, 0.3);\n    --low: rgba(200, 209, 220, 0.5);\n    --date: rgb(123, 122, 122);\n    --sort: beige;\n    --complete: lightgreen;\n    --todo-hover: rgba(0, 0, 0, 0.7);\n    --add-task: rgb(241, 239, 239);\n    --placeholder-clr: rgba(128, 128, 128, 0.5);\n    --confirm: rgba(0, 128, 0, 0.4);\n    --cancel: rgba(255, 0, 0, 0.4);\n    --input-bckgrnd-clr: white;\n  }\n\n\n  :root.dark {\n    --bckgrnd-clr: rgb(66, 66, 66);\n    --txt-clr: white;\n    --highlight-clr: rgba(128, 128, 128, 0.7);\n    --nav-color: #1b1b1b;\n    --svg-hover: white;\n    --input-txt: white;\n    --input-clr: rgb(66, 66, 66);\n    --focus-clr: red;\n    --dynamic-btn-clr: rgba(255, 255, 255, 0.4);\n    --high: rgba(255, 107, 107, 0.5);\n    --normal: rgba(168, 157, 234, 0.5);\n    --low: rgba(200, 209, 220, 0.5);\n    --date: rgba(255, 255, 255, 0.7);\n    --sort: rgba(128, 128, 128, 0.7);\n    --complete: rgba(144, 238, 144, 0.6);\n    --todo-hover: rgba(255, 255, 255, 1);\n    --add-task: rgba(128, 128, 128, 0.7);\n    --placeholder-clr: rgba(255, 255, 255, 0.377);\n    --confirm: rgba(0, 128, 0, 0.9);\n    --cancel: rgba(255, 0, 0, 0.7);\n    --input-bckgrnd-clr: var(--sort);\n  }\n  \n\n\n  body {\n    display: flex;\n    flex-direction: column;\n    height: 100dvh;\n    font-family: \"Roboto\", sans-serif;\n    color: var(--txt-clr);\n  }\n\n  header {\n    padding: 1rem;\n    background-color: var(--nav-color);\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n  }\n\n\n  header button {\n    background-color: var(--bckgrnd-clr);\n  }\n\n  .switch-theme {\n    display: flex;\n    align-items: center;\n    border-radius: 10px;\n    font-weight: bold;\n    border: 1px solid var(--txt-clr);\n  }\n\n\n  .switch-theme svg {\n    width: 2rem;\n    fill: var(--txt-clr);\n  }\n\n  .sort-button {\n    display: flex;\n    align-items: center;\n    border-radius: 10px;\n    font-weight: bold;\n    border: 1px solid var(--txt-clr);\n  }\n\n  .sort-button svg {\n    width: 2rem;\n    fill: var(--txt-clr);\n    margin-right: 0.3rem;\n  }\n\n  main {\n    flex: 1;\n    display: grid;\n    grid-template-columns: 1fr 4.2fr;\n  }\n\n  nav {\n    background-color: var(--nav-color);\n    padding-top: 0.7rem;\n  }\n\n\n  nav h3 {\n    border-radius: 10px;\n    padding: 0.5rem 1rem;\n  }\n\n  .project-div button {\n    border-radius: 10px;\n    border: none;\n    background-color: rgba(255, 255, 255, 0);\n  }\n\n  .projects h3 svg {\n    width: 2rem;\n    margin-right: 0.4rem;\n  }\n\n  .projects h3 {\n    display: flex;\n    justify-content: center;\n  }\n\n\n  .project-div:hover {\n    background-color: var(--highlight-clr);\n    color: white;\n    scale: 1.02;\n  }\n\n\n  .top-nav {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n  }\n\n  .sort-button {\n    margin-right: 10rem;\n    padding: 0.5rem 0.5rem;\n    font-weight: bold;\n    border-radius: 10px;\n    background-color: var(--sort);\n  }\n\n\n  .project-name svg {\n    width: 2.5rem;\n    margin-right: 0.4rem;\n    fill: var(--txt-clr);\n  }\n\n  .sort-button:hover {\n    scale: 1.1;\n  }\n\n  .project-name {\n    display: flex;\n    font-size: 2rem;\n  }\n\n\n  .projects h3:first-child {\n    font-size: 1.5rem;\n    fill: var(--txt-clr);\n  }\n  \n\n  .dates {\n    display: flex;\n    flex-direction: column;\n    padd